<!-- ChatInput.vue -->
<template>
    <div class="chat-input">
        <!--  -->
        <p v-if="isChatLoading">...</p>
        <!--  -->
        <v-text-field  v-else  v-model="input" @keyup.enter="send" placeholder="..." min-width="400"/>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    data() {
        return { input: '' }
    },
    computed: {
        ...mapState({
            isChatLoading: state => state.chessStore.isChatLoading,
        }),
    },
    methods: {
        send() {
            this.$emit('send', this.input)
            this.input = ''
        },
    },
}
</script>
  