<template>
    <div class="promotion-dialog">
        <p></p>
        <div class="promotion-option" @click="select('Queen')"></div>
        <div class="promotion-option" @click="select('Rook')"></div>
        <div class="promotion-option" @click="select('Bishop')"></div>
        <div class="promotion-option" @click="select('Knight')"></div>
    </div>
</template>

<script>
export default {
    name: 'PromotionDialog',
    methods: {
        select(pieceType) {
            // console.log('select:'+pieceType);
            this.$emit('select', { pieceType });
        }
    }
}
</script>

<style scoped>
.promotion-option {
    cursor: pointer;
}
</style>