<template>
  <section id="boardgame">
    <v-row>
      <v-col cols="12" md="6">
        <v-card class="elevation-2 my-4">
          <v-card-title class="headline">AI</v-card-title>
          <v-divider></v-divider>
          <v-list>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-checkbox-multiple-blank-circle</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <router-link :to="`/reversi`"></router-link>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-chess-queen</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <router-link :to="`/chess`"></router-link>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <v-card class="elevation-2 my-4">
          <v-card-title class="headline"></v-card-title>
          <v-divider></v-divider>
          <v-list>
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-hammer-wrench</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <router-link :to="`/buildersTactics`">Builders Tactics</router-link>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>
    </v-row>
    <v-spacer></v-spacer>
  </section>

  <section id="meeplebot">
    <v-card class="my-4">
      <v-card-title></v-card-title>
      <v-row>
        <v-col cols="12" md="3">
          <v-img :src="meepleBotFriend" alt="" :width="300"></v-img>
        </v-col>
        <v-col cols="12" md="3">
          <v-img :src="meepleBotBoardGame" alt="" :width="300"></v-img>
        </v-col>
        <v-col cols="12" md="3">
          <v-img :src="meepleBotGPTCore" alt="" :width="300"></v-img>
        </v-col>
        <v-col cols="12" md="3">
          <v-img :src="meepleBotStrategy" alt="" :width="300"></v-img>
        </v-col>
      </v-row>
      <v-card-text>
        <p>GPTGenerative Pre-trained Transformer</p>
        <p>GPT</p>
        <p></p>
        <p></p>
      </v-card-text>
    </v-card>
    <v-spacer></v-spacer>
  </section>

  <section id="articles">
    <v-card class="my-4">
      <v-card-title></v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <v-chip-group>
              <v-chip v-for="tag in availableTags" :key="tag" :class="{ 'selected': selectedTag === tag }" @click="filterByTag(tag)">
                {{ tag }}
              </v-chip>
            </v-chip-group>
          </v-col>
          <v-col cols="12" md="6" lg="4" v-for="article in filteredArticles" :key="article.id">
            <v-card>
              <v-img :src="article.imageUrl" :alt="article.title" :width="400"></v-img>
              <v-card-title>{{ article.title }}</v-card-title>
              <v-card-actions>
                <router-link :to="`/articles/${article.id}`">Read more</router-link>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-spacer></v-spacer>
  </section>
</template>

<script setup>
import { useStore } from 'vuex';
import { computed, ref } from 'vue';

// 
import meepleBotFriend from '@/assets/MeepleBot/MeepleBotFriend.webp';
import meepleBotBoardGame from '@/assets/MeepleBot/MeepleBotBoardGame.jpg';
import meepleBotGPTCore from '@/assets/MeepleBot/MeepleBotGPTCore.webp';
import meepleBotStrategy from '@/assets/MeepleBot/MeepleBotStrategy.webp';

// 
const store = useStore();

// 
const articles = computed(() => {
  const articlesData = store.state.articleStore.articles;
  // console.log('Fetched articles:', articlesData);
  return articlesData;
});
const selectedTag = ref(null);

// 
const filterByTag = tag => {
  // console.log("Current selectedTag.value:", selectedTag.value);
  // console.log("Clicked tag:", tag);

  if (String(selectedTag.value) === String(tag)) {
    // console.log("Tags match. Clearing selection.");
    selectedTag.value = null;  // 
  } else {
    // console.log("Tags do not match. Setting new selection.");
    selectedTag.value = tag;
  }

  // console.log("Updated selectedTag.value:", selectedTag.value);
};

  
const filteredArticles = computed(() => {
  // console.log("Articles before filtering:", articles.value);
  if (selectedTag.value === null) {
    return articles.value;
  }
  const filtered = articles.value.filter(article => article.tags.includes(selectedTag.value));
  // console.log("Filtered articles:", filtered);
  return filtered;
});

// computed properties
const availableTags = computed(() => {
  const tags = new Set();
  articles.value.forEach(article => {
    article.tags.forEach(tag => tags.add(tag));
  });
  return Array.from(tags);
});
</script>
