<template>
  <div>
    <!--  -->
    <p v-if="isNextMoveLoading">...</p>
    <!--  -->
    <v-btn v-else @click="askAINextMove"></v-btn>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  name: 'AINextMoveButton',
  computed: {
    ...mapState({
      isNextMoveLoading: state => state.chessStore.isNextMoveLoading,
    }),
  },
  methods: {
    ...mapActions('chessStore', ['getNextMove']),
        
    askAINextMove() {
      // VuexgetNextMove
      this.getNextMove();
    }
  }
};
</script>
