<template>
    <div id="player-indicator">
      <div v-if="winner === 'Draw'"></div>
      <div v-else-if="winner === 'White'"></div>
      <div v-else-if="winner === 'Black'"></div>
      <div v-else>
        : {{ currentPlayer }}
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      currentPlayer: String,
      winner: String
    }

  };
  </script>
  
  <style>
  #player-indicator {
    margin-bottom: 20px;
  }
  </style>
  